module.exports = "\n/* Sentence Translation */\n\n.sentence-translation {\n    width: 100%;\n    height: 100%;\n    background: linear-gradient(45deg, #4B4168, #03618ae8);\n    overflow: auto;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.translation__box {\n    background: #fff;\n    border-radius: 20px;\n    box-shadow: 0 0 3em rgba(0, 0, 0, 0.3);\n    max-width: 1200px;\n    overflow: auto;\n    position: relative;\n    transition: all .3s ease;\n    width: 90vw;\n    height: 90vh;\n    padding: 16px;\n    box-sizing: border-box;\n}\n\n.translate__info h2,\n.translate__msg h1 {\n    margin: 0;\n    line-height: normal;\n}\n\n.translate__info {\n    padding: 16px;\n    display: flex;\n    align-items: center;\n}\n\n.translate__info h2 {\n    margin-right: auto;\n}\n\n.translate__info .replay-home {\n    width: 32px;\n    height: 32px;\n    cursor: pointer;\n}\n\n.replay-home img {\n    width: 100%;\n}\n\n.translate__msg {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 8px 16px;\n    border: 1px solid #79b5da;\n    background: #d3e2ea;\n    border-radius: 20px;\n    color: #245073;\n    margin-bottom: 16px;\n}\n\n.user-information {\n    display: flex;\n}\n\n/* .user-information>div:first-child {\n    padding-right: 16px;\n}\n\n.user-information>div:last-child {\n    padding-left: 16px;\n} */\n\n.user-information .user-input,\n.user-information .bot-input {\n    flex: 1 auto;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.user-input h1,\n.bot-input h1 {\n    margin: 0;\n}\n\n/* .user-input span,\n.bot-input span {\n    border: 1px dashed #024f9d;\n    border-radius: 5px;\n    padding: 8px 16px;\n    font-size: 24px;\n} */\n\n.score-card,\n.decide-answer {\n    display: grid;\n    padding: 16px 0;\n    grid-gap: 16px;\n    grid-template-columns: repeat(2, 1fr);\n}\n\n.record__audio h2 {\n    margin: 0;\n}\n\n.record__audio .record__audio__bot {\n    flex-direction: row-reverse;\n}\n\n/* .record__audio__bot {\n    flex-direction: row;\n    padding-left: 16px;\n} */\n\n.arrow-image {\n    width: 64px;\n    height: 64px;\n    margin: 0 16px;\n}\n\n.arrow-image img {\n    width: 100%;\n}\n\n.record__audio {\n    display: flex;\n    justify-content: space-around;\n}\n\n.record__audio__user,\n.record__audio__bot {\n    flex: 1 50%;\n}\n\n.record__audio__user,\n.record__audio__bot,\n.question__set__user,\n.question__set__bot,\n.score-card__user,\n.score-card__bot,\n.decide-answer__user,\n.decide-answer__bot {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.audio-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin-right: 16px;\n    cursor: pointer;\n}\n\n.record__audio__bot .audio-container {\n    margin: 0 0 0 16px;\n}\n\n.audio-container .btn {\n    margin-top: 8px;\n}\n\n.audio-icon,\n.bot-icon {\n    width: 64px;\n    height: 64px;\n    background: #d3e2ea;\n    border-radius: 50%;\n    box-sizing: border-box;\n    cursor: pointer;\n    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);\n}\n\n.audio-icon {\n    padding: 12px;\n}\n\n.bot-icon {\n    padding: 0;\n}\n\n.audio-icon img,\n.bot-icon img {\n    width: 100%;\n}\n\n.question__set__container {\n    padding: 16px 0;\n    /* min-height: 180px; */\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.question__set {\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    padding: 8px;\n    border-bottom: 1px dashed #024f9d;\n}\n\n.question__set__user, .question__set__bot {\n    flex: 1 1 50%;\n}\n\n.question__set__user {\n    padding-left: 40px;\n}\n\n.question__set__bot {\n    padding-right: 40px;\n}\n\n.question__set__user,\n.question__set__bot {\n    position: relative;\n    flex-wrap: wrap;\n}\n\n.question__status {\n    position: absolute;\n}\n\n.question__status--left {\n    left: 0;\n}\n\n.question__status--right {\n    right: 0;\n}\n\n.icon {\n    position: relative;\n    width: 24px;\n    height: 24px;\n    display: inline-block;\n}\n\n.icon::after {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    font-size: 20px;\n    color: white;\n}\n\n.icon--right {\n    border: 1px solid #8cf391;\n    background: #8cf391;\n}\n\n.icon--right::after {\n    content: '\\2713';\n}\n\n.icon--wrong {\n    border: 1px solid #e21f11;\n    background: #e21f11;\n}\n\n.icon--wrong::after {\n    content: '\\2718';\n}\n\n.icon--right,\n.icon--wrong {\n    border-radius: 50%;\n}\n\n.decide-answer__user,\n.decide-answer__bot {\n    justify-content: space-evenly;\n}\n\n.submit-btn {\n    padding: 16px 0;\n    display: flex;\n    justify-content: center;\n}\n\n.m-0 {\n    margin: 0 !important;\n}\n\n.p-0 {\n    padding: 0 !important;\n}\n/*# sourceMappingURL=data:application/json;base64, */"